.modal-wrapper {
    background: white;
    min-width: 200px;
    max-width: 200px;
}

.flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
}

.flex-stops {
    -webkit-order: 0;
            order: 0;
    -webkit-flex-grow: 2;
            flex-grow: 2;
}

.flex-info {
    -webkit-order: 1;
            order: 1;
    padding-left: 20px;
}

.modal-content section {
    padding: 5px;
}

.modal-content header h4 {
    margin: 0px;
    line-height: 38px;
}

.modal-content header {
    background-color: #E0D0C1;
    padding: 5px;
    position: relative;
    height: 40px;
}

.modal-content header .close-button-container {
    position: absolute;
    top: 10px;
    right: 10px;
}

.modal-content header .close-button-container button {
    background: darkgrey;
    font-size: 20px;
}
.stop {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 0;
  border-top: 1px solid black; }

.stop-wrapper {
  margin-bottom: 0; }

table {
  width: 100%; }

table th, table td {
  padding: 10px;
  text-align: left; }

.stops-reload-button {
  position: absolute;
  right: 7px;
  top: -2px; }

.reload-button {
  background: darkgrey;
  display: inline-block;
  padding-top: 3px;
  padding-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
  cursor: pointer; }

.reload-button span {
  vertical-align: middle;
  line-height: 25px;
  text-align: center; }

.count-down-label {
  margin-left: 5px;
  font-size: 16px; }

